<template>
    <div>
        <div class="custom-button">
            <button @click="openForm" class="slds-button slds-button_success">{{ buttonLabel }}</button>
        </div>
        <transition @before-enter="collapse" @enter="expand" @after-enter="resetHeight" @before-leave="expand"
        @leave="collapse">
        <div v-if="isOpen" class="slds-section__content_animated slds-p-horizontal_xxx-small">
            <slot />
        </div>
        </transition>
    </div>
</template>
<script>
export default {
  name: 'collape',
  props: ['buttonLabel'],
  data () {
    return {
      isOpen: false
    }
  },
  methods: {
    openForm () {
      this.isOpen = !this.isOpen
    },
    collapse (element) {
      element.style.overflow = ''
      element.style.height = 0
      element.style.opacity = 0
    },
    expand (element) {
      element.style.overflow = ''
      element.style.height = element.scrollHeight + 'px'
      // element.scrollHeight
      element.style.opacity = 1
    },
    resetHeight (element) {
      element.style.height = null
      element.style.overflow = 'initial'
    }
  }
}
</script>

<style src="../assets/style.css"></style>

<style scoped>
.slds-section__title-action:disabled:hover {
  cursor: not-allowed;
  background: #f3f2f2;
  color: #dddbda;
}
.slds-section__title-action-icon {
  transition: all 300ms ease;
}
.slds-section__content_animated {
  overflow: hidden;
  transition: all 300ms ease;
}
.slds-section.slds-is-open .slds-section__content_animated {
  padding-top: 0.5rem;
  transition: all 300ms ease-out;
}
.slds-button__icon {
  width: auto;
  height: auto;
}
.custom-button {
    text-align: end;
    padding: 15px;
    background-color: rgb(243, 243, 243);
}
</style>



