<template>
  <div class="create-sub-order">
    <modal-confirm id="discard" ref="modalConfirmDiscard" :modalConfirmOk="modalConfirmDiscardOk" title="discard"/>
    <modal-confirm id="confirm" ref="modalConfirm" :modalConfirmOk="modalConfirmOk"/>
    <modal-std id="model-loder"
        size="small"
        :btnCloseDisp="false"
        @close="$emit('close')"
        ref="modalLoader">
      <template #content>
        <div class="custom-modal-loader">
          <img :src="require('../assets/iconStd/loader.gif')" style="width: 80px"/>
          <p style="margin-top: 20px;"><span class="slds-card__header-link slds-text-heading_small">Please wait...</span></p>
        </div>
      </template>
    </modal-std>
    <div v-if="formMode === 'View'">
      <tab ref="tab" :tabSelect="tabSelect" :actionList="actionList" @onTabChange="onTabChange" @onActionChange="onActionChange"></tab>
      <div v-if="tabSelect === 'detail'">
        <moc-order :order="order" :formMode="formMode" ref="MocOrder"></moc-order>
      </div>
      <div v-else>
        <relate-list :orderList="orderList" :notification="order.Notification__c"></relate-list>
      </div>
    </div>
    <div v-else>
      <moc-order :order="order" :formMode="formMode" ref="MocOrder"></moc-order>
      <footer class="slds-card__footer">
        <div class="row" >
          <div class="col-md-12">
            <button class="slds-button slds-button_neutral" @click="clickCancel">Cancel</button>
            <button class="slds-button slds-button_success" @click="clickSave">Save</button>
          </div>
        </div>
      </footer>
    </div>
  </div>
</template>
<script>
import Tab from '../standardSalesforceCmp/Tab.vue'
import mocOrder from './MocOrder.vue'
import RelateList from './RelateList.vue'
export default {
  name: 'create-order',
  data () {
    return {
      isOpen: false,
      recId: '',
      formMode: '',
      resultSuccess: {},
      tabSelect: 'detail',
      actionList: [],
      // selectedAction: '',
      loading: true
    }
  },
  created () {
    this.$store.dispatch('receiveLookupFncLocation', '')
    this.$store.dispatch('receiveLookupUser', '')
    this.$store.dispatch('receiveLookupUser2', '')
    this.$store.dispatch('receiveLookupMasterMap', '')
    this.$store.dispatch('receiveLookupMasterMapMain', '')
    this.$store.dispatch('receiveLookupMasterMapPlant', '')
    this.handleActionList()
  },
  watch: {
    'order.Integration_Status__c' () {
      this.handleActionList()
    }
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.recId = urlParams.get('id')
    const urlMode = urlParams.get('formMode')
    if (urlMode === null) {
      this.formMode = 'View'
    } else if (urlMode !== '') {
      this.formMode = urlParams.get('formMode')
    } else {
      this.formMode = 'New'
    }
    console.log('urlMode', urlMode, 'formMode', this.formMode)
    // this.formMode = urlParams.get('formMode') || 'New'
    let paramSearch = {
      emocApplicationId: this.recId,
      orderId: this.recId
    }
    // console.log('formMode || ', urlParams.get('formMode'), 'recId || ', this.recId, 'queryString|| ', queryString)
    if (this.recId && this.formMode === 'New') {
      this.$store.dispatch('receiveOrder', paramSearch).then(() => {
        this.loading = false
      })
    } else if (this.formMode === 'View' || this.formMode === 'Edit') {
      this.$store.dispatch('receiveOrderDetail', {sendParam: paramSearch, afterSet: () => {}, closePopupLoader: this.closePopupLoader}).then(() => {
        this.loading = false
        setTimeout(() => {
          this.showPopupLoader()
          this.onLoading(paramSearch)
        }, 1000)
      })
    }
  },
  computed: {
    order () { return this.$store.state.order },
    orderList () { return this.$store.state.orderList },
    selectedAction () { return this.$store.state.selectedAction }
  },
  methods: {
    onLoading (paramSearch) {
      setTimeout(() => {
        this.$store.dispatch('receiveOrderDetail', {sendParam: paramSearch, afterSet: () => {}, closePopupLoader: this.closePopupLoader})
        // this.closePopupLoader()
      }, 2000)
    },
    modalConfirmDiscardOk () {
      this.clickSave()
    },
    handleActionList () {
      let dataOrder = this.order
      this.actionList = [
        {
          value: 'Edit',
          label: 'Edit',
          disabled: !(dataOrder.Integration_Status__c !== 'Success')
        },
        {
          value: 'Close',
          label: 'Close',
          disabled: !(dataOrder.Integration_Status__c === 'Success') || dataOrder.Order_Status__c === 'Closed' || dataOrder.disableBtnClose === true
        },
        {
          value: 'Create Sub Order',
          label: 'Create Sub Order',
          disabled: !(dataOrder.Integration_Status__c === 'Success')
        },
        {
          value: 'Discard',
          label: 'Discard',
          disabled: !(dataOrder.Integration_Status__c !== 'Success')
        },
        {
          value: 'Resend',
          label: 'Resend',
          disabled: !(dataOrder.Integration_Status__c !== 'Success')
        }
      ]
    },
    onActionChange (value) {
      // this.selectedAction = value
      this.$store.commit('setSelectedAction', value)
      // console.log('onActionChange ', value)
      if (value === 'Edit') {
        this.redirectLink('linkToEdit')
      } else if (value === 'Create Sub Order') {
        this.redirectLink('linkToCreateSubOrder')
      } else if (value === 'Discard') {
        this.$refs.modalConfirmDiscard.onOpen()
      } else if (value === 'Resend') {
        this.clickSave()
      } else if (value === 'Close') {
        this.clickSave()
      }
    },
    onTabChange (value) {
      this.tabSelect = value
    },
    modalConfirmOk () {
      this.showPopupLoader()
      setTimeout(() => {
        this.closePopupLoader()
        this.redirectLink('linkGoBack')
      }, 1000)
    },
    clickCancel () {
      this.$refs.modalConfirm.onOpen()
    },
    showPopupLoader () {
      this.$refs.modalLoader.onOpen()
    },
    closePopupLoader () {
      this.$refs.modalLoader.onClose()
    },
    clickSave () {
      Promise.all([
        this.$refs.MocOrder.$validator.validateAll()
      ]).then((result) => {
        // console.log('result: ', result)
        if (result.filter(df => !df).length === 0) {
          let paramSave = {}
          let apiName = ''
          let linkTo = ''
          if (this.formMode === 'New') {
            paramSave = {
              order: {
                ...this.order,
                Integration_Status__c: 'In Progress',
                Start_Date__c: this.formatDate(this.order.Start_Date__c),
                Finished_Date__c: this.formatDate(this.order.Finished_Date__c)
              }
            }
            paramSave = JSON.stringify(paramSave)
            apiName = 'saveOrder'
            linkTo = 'linkToDetail'
          } else if (this.formMode === 'Edit') {
            paramSave = {
              Functional_Location__c: this.order.Functional_Location__c,
              Functional_Location_Description__c: this.order.Functional_Location_Description__c,
              mainWorkCenterPlant__c: this.order.mainWorkCenterPlant__c,
              PM_Activity_Type__c: this.order.PM_Activity_Type__c,
              Responsible_Person_Name__c: this.order.Responsible_Person_Name__c,
              Responsible_Person_ID__c: this.order.Responsible_Person_ID__c,
              Start_Date__c: this.formatDate(this.order.Start_Date__c),
              Finished_Date__c: this.formatDate(this.order.Finished_Date__c),
              Main_Work_Center__c: this.order.Main_Work_Center__c,
              Responsible_person_PISUser__c: this.order.Responsible_person_PISUser__c
            }
            apiName = 'putOrder'
            linkTo = 'linkToDetail'
          } else if (this.selectedAction === 'Resend') {
            paramSave = {
              Integration_Status__c: 'In Progress',
              Order_Status__c: 'In Progress'
            }
            apiName = 'putOrder'
            linkTo = ''
          } else if (this.selectedAction === 'Close') {
            paramSave = {
              Id: this.order.Id
              // Integration_Status__c: 'In Progress',
              // Order_Status__c: 'Closed'
            }
            apiName = 'callCloseOrderStatus'
            linkTo = ''
          } else if (this.selectedAction === 'Discard') {
            apiName = 'deleteOrder'
            linkTo = 'linkGoBack'
          }
          this.onSaveOrder(apiName, paramSave, linkTo)
        }
      })
    },
    onSaveOrder (apiName, paramSave, linkTo) {
      console.log(apiName, paramSave, linkTo)
      if (this.formMode !== 'New') {
        paramSave = {...paramSave, Id: this.recId}
      }
      this.showPopupLoader()
      this.$store.dispatch(apiName, paramSave).then(result => {
        console.log('onSaveOrder: ', result)
        if (result.statusSuccess) {
          this.resultSuccess = result
          setTimeout(() => {
            if (linkTo) {
              this.redirectLink(linkTo, result.Id)
              this.closePopupLoader()
            } else {
              this.onGetOrder()
            }
            this.$refs.MocOrder.setFlagAlert(false)
          }, 3000)
        }
      })
    },
    onGetOrder () {
      let paramSearch = {
        emocApplicationId: this.recId,
        orderId: this.recId
      }
      this.$store.dispatch('receiveOrderDetail', {sendParam: paramSearch, afterSet: () => {}, closePopupLoader: this.closePopupLoader})
    },
    redirectLink (linkName, id) {
      this.$store.dispatch('sendMsg', {
        action: linkName,
        recordId: id || this.recId
      })
    },
    formatDate (date) {
      if (!date) return null
      const [year, month, day] = date.split('/')
      return `${day}-${month}-${year}`
    }
  },
  components: {
    mocOrder,
    Tab,
    RelateList
  }
}
</script>

<style src="../assets/style.css"></style>

<style scoped>
.custom-modal-loader {
    text-align: center;
    margin: 20;
  }
</style>



