<template>
  <div name="moc-order">
      <div class="data-frame  margin-form">
        <div class="slds-card__header slds-grid header custom-border-header custom-position">
          <header class="slds-media slds-media_center slds-has-flexi-truncate">
            <div class="slds-media__body custom-h2-noti" >
              <h2>
                <span class="slds-card__header-link slds-text-heading_small">{{ formMode || 'New' }} Order</span>
                <!-- <span class="slds-card__header-link slds-text-heading_small">{{emocEditEnable ? 'Edit E-MOC' : 'Create E-MOC'}}</span> -->
              </h2>
            </div>
          </header>
        </div>
        <div v-if="order.Integration_Status__c !== 'Success' && order.Integration_Status__c !== '' && !flagAlert && formMode === 'View'" class="row">
          <div class="col-sm-12" style="background-color: red; text-align: center; color: white; font-size: 13px; position: relative">
              <span>
                <i class="fa fa-ban" aria-hidden="true" ></i>
              </span>
              <span>
                &nbsp; {{order.Integration_Message__c || 'Integration Failed: Your network could not connect to Server. Please contact System Administer.'}}
              </span>
              <span style="position: absolute; right: 10px;">
                <button @click="setFlagAlert(!flagAlert)">
                  X
                </button>
              </span>
          </div>
        </div>
         <div class="row">
          <div class="col-sm-6">
            <input-text-std v-model="order.Order_Type__c" label="Order Type" disabled required>
              <template #error>
                <span>{{ errors.first('Order_Type__c') ? 'Complete this field.' : '' }}</span>
              </template>
            </input-text-std>
          </div>
          <div v-show="formMode !== 'New'" class="col-sm-6">
            <input-text-std v-model="order.Order_Number__c" label="Order Number" disabled></input-text-std>
          </div>
        </div>
        <div class="row" v-show="formMode !== 'New'" >
          <div class="col-sm-6">
            <input-text-std v-model="order.Order_Status__c" label="Order Status" disabled></input-text-std>
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="order.Notification__c" label="Notification" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <lookups-std v-model="order.Functional_Location__c"
                          v-validate="'required'"
                          data-vv-as="Functional_Location__c"
                          data-vv-value-path="inputValue"
                          label="Functional Location"
                          placeholder="Search..."
                          :options="optionsLookupFunctionalLocation"
                          :required="true"
                          :error="errors.has('Functional_Location__c')"
                          name="Functional_Location__c"
                          @search="filterLookupFunctionalLocation(arguments[0])"
                          icon="account"
                          :disabled="formMode === 'View'"
                          loading>
              <template #error>
                <span>{{ errors.first('Functional_Location__c') ? 'Complete this field.' : ''   }}</span>
              </template>
            </lookups-std>
          </div>
          <div class="col-sm-6">
              <input-text-std
                v-model="order.Functional_Location_Description__c"
                v-validate="'required|max:40'"
                data-vv-as="Functional_Location_Description__c"
                data-vv-value-path="inputValue"
                name="Functional_Location_Description__c"
                label="Description"
                :error="errors.has('Functional_Location_Description__c')"
                :maxlength="40"
                disabled
              >
                <template #error>
                  <span>{{ errors.first('Functional_Location_Description__c') ? 'Complete this field.' : '' }}</span>
                </template>
              </input-text-std>
            </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
             <combobox-std
              v-model="order.mainWorkCenterPlant__c"
              data-vv-as="mainWorkCenterPlant__c"
              data-vv-value-path="textInputValue"
              name="mainWorkCenterPlant__c"
              :error="errors.has('mainWorkCenterPlant__c')"
              :options="optionsLookupMasterMapPlant"
              label="Plant of Main Work Center"
              @search="filterLookupMasterMapPlant(arguments[0])"
              :disabled="disabledField"
            >
            </combobox-std> 
            <!-- <input-text-std v-model="order.mainWorkCenterPlant__c" label="Plant of Main Work Center" disabled></input-text-std> -->
          </div>
          <!-- <div class="col-sm-6">
            <input-text-std v-model="order.Main_Work_Center__c" label="Main Work Center" disabled></input-text-std>
          </div> -->
          <div class="col-sm-6">
            <combobox-std
              v-model="order.Main_Work_Center__c"
              data-vv-as="Main_Work_Center__c"
              data-vv-value-path="textInputValue"
              name="Main_Work_Center__c"
              :error="errors.has('Main_Work_Center__c')"
              :options="optionsLookupMasterMapMain"
              label="Main Work Center"
              @search="filterLookupMasterMapMain(arguments[0])"
              :disabled="disabledField"
            >
            </combobox-std> 
          </div>
     <!--      <div class="col-sm-6">
            <input-text-std v-model="order.Requestor_ID__c" label="Requestor" disabled></input-text-std>
          </div> -->
        </div>
        <div class="row">
          <div class="col-sm-6">
            <input-text-std v-model="order.E_MOC_Application_No__c" label="E-MOC Application No." disabled></input-text-std>
          </div>
       <!--    <div class="col-sm-6">
            <input-text-std v-model="order.wbs_element__c" label="Budget Number (WBS element)" :classCustom="'text-transform: uppercase;'" disabled></input-text-std>
          </div> -->
          <div class="col-sm-6">
            <input-text-std
              v-model="order.wbs_element__c"
              v-validate="'required'"
              data-vv-as="Budget Number (WBS element)"
              data-vv-value-path="inputValue"
              name="wbs_element__c"
              label="Budget Number (WBS element)" 
              :classCustom="'text-transform: uppercase;'"
              :error="errors.has('wbs_element__c')"
              :required="true"
              disabled
            >
              <template #error>
                <span>{{ errors.first('wbs_element__c') }}</span>
              </template>
            </input-text-std>
          </div>
          <!-- <div class="col-sm-6">
            <input-currency-std
                            :disabled="disabledField"
                            v-model="order.wbs_element__c"
                            label="Budget Number (WBS element)"
                            addonPost="THB">
            </input-currency-std>
          </div> -->
        </div>
        <div class="row">
          <div class="col-sm-6">
            <lookups-std v-model="order.PM_Activity_Type__c"
                         data-vv-as="PM Activity Type"
                         data-vv-value-path="inputValue"
                         label="PM Activity Type"
                         placeholder="Search..."
                         :options="optionsLookupMasterMap"
                         :error="errors.has('PM_Activity_Type__c')"
                         name="PM_Activity_Type__c"
                         @search="filterLookupMasterMap(arguments[0])"
                         icon="actions_and_buttons"
                         :disabled="disabledField"
                         loading>
              <template #error>
                <span>{{ errors.first('PM_Activity_Type__c') ? 'Complete this field.' : '' }}</span>
              </template>
            </lookups-std>
          </div>
          <div class="col-sm-6">
            <!-- <input-text-std v-model="order.Requestor_ID__c" label="Requestor" disabled></input-text-std> -->
            <lookups-std v-model="order.Requestor_Name__c"
                         data-vv-as="Requestor"
                         data-vv-value-path="inputValue"
                         label="Requestor"
                         placeholder="Search..."
                         :options="optionsLookupUser2"
                         :required="true"
                         :error="errors.has('Requestor_Name__c')"
                         name="Requestor_Name__c"
                         @search="filterLookupUser2(arguments[0])"
                         icon="people"
                         disabled
                         loading>
            </lookups-std>
          </div>
        </div>
        <div class="row">
           <div class="col-sm-6">
              <label class="slds-form-element__label">
                 <abbr class="slds-required" title="required">*</abbr>Start date
              </label>
              <v-menu v-model="showPickerStartDate"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    class="custom-style"
                    v-model="order.Start_Date__c"
                    :required="true"
                    v-validate="'required'"
                    data-vv-as="Start_Date__c"
                    data-vv-value-path="inputValue"
                    name="Start_Date__c"
                    :error="errors.has('Start_Date__c')"
                    @blur="dateStart = parseDate(order.Start_Date__c)"
                    placeholder="dd/mm/yyyy"
                    v-bind="attrs"
                    v-on="on"
                    append-icon="mdi-calendar"
                    autocomplete="off"
                    hide-details
                    outlined
                    dense
                    :disabled="disabledField"
                  ></v-text-field>
                </template>
                <v-date-picker
                  no-title
                  v-model="dateStart"
                  @change="showPickerStartDate = false"
                  :min="minCalendar"
                ></v-date-picker>
              </v-menu>
              <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Start_Date__c') ? 'Complete this field.' : '' }}</span></div>
            </div>
            <!-- <div class="col-sm-6">
              <label class="slds-form-element__label">
                 <abbr class="slds-required" title="required">*</abbr>Finished date
              </label>
              <v-menu v-model="showPickerEndDate"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    class="custom-style"
                    v-model="order.Finished_Date__c"
                    :required="true"
                    v-validate="'required'"
                    data-vv-as="Finished_Date__c"
                    data-vv-value-path="inputValue"
                    name="Finished_Date__c"
                    :error="errors.has('Finished_Date__c')"
                    @blur="finishedDate = parseDate(order.Finished_Date__c)"
                    placeholder="dd/mm/yyyy"
                    v-bind="attrs"
                    v-on="on"
                    append-icon="mdi-calendar"
                    autocomplete="off"
                    hide-details
                    outlined
                    dense
                    :disabled="disabledField"
                  ></v-text-field>
                </template>
                <v-date-picker
                  no-title
                  v-model="finishedDate"
                  @change="showPickerEndDate = false"
                  :min="minCalendarEndDate"
                ></v-date-picker>
              </v-menu>
              <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Finished_Date__c') ? 'Complete this field.' : '' }}</span></div>
            </div> -->
        </div>
        <div class="row">
          <div class="col-sm-6">
              <lookups-std v-model="order.Responsible_Person_Name__c"
                        v-validate="'required'"
                        data-vv-as="Responsible_Person_Name__c"
                        :error="errors.has('Responsible_Person_Name__c')"
                        data-vv-value-path="inputValue"
                        label="Responsible Person Name"
                        placeholder="Search..."
                        :options="optionsLookupUser"
                        name="Responsible_Person_Name__c"
                        @search="filterLookupUser(arguments[0])"
                        icon="people"
                        loading
                        :disabled="disabledField"
                        >
                <template #error>
                  <span>{{ errors.first('Responsible_Person_Name__c') ? 'Complete this field.' : '' }}</span>
                </template>
              </lookups-std>
          </div>
          <div class="col-sm-6">
              <input-text-std v-model="order.Responsible_Person_ID__c" label="Responsible Person ID" disabled></input-text-std>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
import moment from 'moment'
export default {
  name: 'moc-order',
  props: [
    'order', 'formMode'
  ],
  data () {
    return {
      showPickerStartDate: false,
      showPickerEndDate: false,
      dateStart: '',
      finishedDate: '',
      minCalendar: moment().format('YYYY-MM-DD'),
      minCalendarEndDate: moment().format('YYYY-MM-DD'),
      disabledField: '',
      flagAlert: false
    }
  },
  created () {
    if (this.order.Responsible_Person_Name__c !== '' && this.order.Responsible_Person_Name__c) {
      this.filterLookupUser(this.order.Responsible_Person_Name__c)
    }
    if (this.order.Main_Work_Center__c !== '' && this.order.Main_Work_Center__c) {
      this.filterLookupMasterMapMain(this.order.Main_Work_Center__c)
    }
    if (this.order.mainWorkCenterPlant__c !== '' && this.order.mainWorkCenterPlant__c) {
      this.filterLookupMasterMapPlant(this.order.mainWorkCenterPlant__c)
    }
    if (this.order.PM_Activity_Type__c !== '' && this.order.PM_Activity_Type__c) {
      this.filterLookupMasterMap(this.order.PM_Activity_Type__c)
    }
    if (this.order.Requestor_Name__c !== '' && this.order.Requestor_Name__c) {
      this.filterLookupUser2(this.order.Requestor_Name__c)
    }
  },
  watch: {
    'formMode' (newVal) {
      this.disabledField = newVal === 'View'
    },
    dateStart (val) {
      this.order.Start_Date__c = this.formatDate(this.dateStart)
      this.minCalendarEndDate = this.dateStart
    },
    finishedDate (val) {
      this.order.Finished_Date__c = this.formatDate(this.finishedDate)
    },
    'order.Functional_Location__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupFunctionalLocation(newVal)
        let defaultFomula = this.lookupFncLocation.filter(df => {
          return df.Id === newVal
        })
        if (defaultFomula.length !== 0) {
          this.order.Functional_Location_Description__c = defaultFomula[0].Description__c // this.order.Functional_Location_Description__c || defaultFomula[0].Description__c
          this.order.mainWorkCenterPlant__c = defaultFomula[0].mainWorkCenterPlant__c // this.order.mainWorkCenterPlant__c || defaultFomula[0].mainWorkCenterPlant__c
          this.order.Main_Work_Center__c = defaultFomula[0].MainWorkCenter__c // this.order.Main_Work_Center__c || defaultFomula[0].MainWorkCenter__c
        }
      } else {
        this.filterLookupFunctionalLocation('')
      }
    },
    'order.Responsible_Person_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(this.order.Responsible_Person_Name__c)
        let defaultFomula = this.lookupUser.filter(df => {
          return df.Id === newVal
        })
        if (defaultFomula.length !== 0) {
          this.order.Responsible_Person_ID__c = defaultFomula[0].Employee_Id__c
          this.order.Responsible_person_PISUser__c = defaultFomula[0].PISUsers__c
        }
      } else {
        this.filterLookupUser('')
      }
    },
    'order.PM_Activity_Type__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupMasterMap(newVal)
      }
    },
    'order.Main_Work_Center__c' (newVal) {
      this.filterLookupMasterMapMain(this.order.Main_Work_Center__c)
    },
    'order.mainWorkCenterPlant__c' (newVal) {
      this.filterLookupMasterMapPlant(this.order.mainWorkCenterPlant__c)
    },
    'order.Requestor_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser2(newVal)
      }
    }
  },
  computed: {
    lookupMasterMapMain () { return this.$store.state.lookupMasterMapMain },
    lookupMasterMapPlant () { return this.$store.state.lookupMasterMapPlant },
    lookupMasterMap () { return this.$store.state.lookupMasterMap },
    lookupFncLocation () { return this.$store.state.lookupFncLocation },
    lookupUser () { return this.$store.state.lookupUser },
    lookupUser2 () { return this.$store.state.lookupUser2 },
    optionsLookupMasterMapMain () {
      return this.lookupMasterMapMain.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name
        }
      })
    },
    optionsLookupMasterMapPlant () {
      return this.lookupMasterMapPlant.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name
        }
      })
    },
    optionsLookupMasterMap () {
      return this.lookupMasterMap.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name,
          icon: 'actions_and_buttons'
        }
      })
    },
    optionsLookupFunctionalLocation () {
      return this.lookupFncLocation.map(dm => {
        return {
          value: dm.Id,
          label: dm.Name,
          icon: 'account'
        }
      })
    },
    optionsLookupUser () {
      return this.lookupUser.map(dm => {
        return {
          value: dm.Id,
          label: dm.FirstName + ' ' + dm.LastName,
          icon: 'people'
        }
      })
    },
    optionsLookupUser2 () {
      return this.lookupUser2.map(dm => {
        return {
          value: dm.Id,
          label: dm.FirstName + ' ' + dm.LastName,
          icon: 'people'
        }
      })
    }
  },
  methods: {
    setFlagAlert (flag) {
      this.flagAlert = flag
    },
    filterLookupMasterMapMain (searchParam) {
      this.$store.dispatch('receiveLookupMasterMapMain', searchParam)
    },
    filterLookupMasterMapPlant (searchParam) {
      this.$store.dispatch('receiveLookupMasterMapPlant', searchParam)
    },
    filterLookupMasterMap (searchParam) {
      this.$store.dispatch('receiveLookupMasterMap', searchParam)
    },
    filterLookupFunctionalLocation (searchParam) {
      this.$store.dispatch('receiveLookupFncLocation', searchParam)
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', searchParam)
    },
    filterLookupUser2 (searchParam) {
      this.$store.dispatch('receiveLookupUser2', searchParam)
    },
    parseDate (date) {
      // console.log('parseDate: ', date)
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    }
  }
}
</script>

<style>
.custom-style {
  font-size: .8125rem !important;
}
.theme--light.v-input--is-disabled, .theme--light.v-input--is-disabled input, .theme--light.v-input--is-disabled textarea {
  color: black
}
.theme--light.v-input--is-disabled, .theme--light.v-input--is-disabled textarea .v-input__control{
  background-color: #f3f3f3;
}
.v-text-field .v-input__control .v-input__slot {
  min-height: 20px !important;
}
.v-input__append-inner {
  margin-top: 4px !important;
}
.v-text-field--outlined fieldset {
  border: 1px solid #c9c9c9;
}
.margin-form {
  margin: 0px 30px 30px 30px;
}
.custom-position {
  text-align: center
}
</style>


