import Vue from 'vue'
import Vuex from 'vuex'
import createOrderApi from '../api/createOrder'
// import moment from 'moment'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    lookupMasterMap: [],
    lookupMasterMapMain: [],
    lookupMasterMapPlant: [],
    lookupFncLocation: [],
    lookupUser: [],
    lookupUser2: [],
    orderList: [],
    order: {
      Order_Type__c: 'CR01',
      Functional_Location__c: null,
      Functional_Location_Description__c: null,
      Description__c: null,
      PM_Activity_Type__c: null,
      mainWorkCenterPlant__c: null,
      Main_Work_Center__c: null,
      Responsible_Person_Name__c: null,
      Responsible_Person_ID__c: null,
      Responsible_person_PISUser__c: null,
      Start_Date__c: null,
      Finished_Date__c: null,
      wbs_element__c: '',
      E_MOC_Application_No__c: null,
      Integration_Status__c: '',
      Notification__c: null,
      Order_Status__c: null,
      User_Status__c: null,
      Order_Number__c: null,
      Requestor__c: '',
      Requestor_Name__c: '',
      disableBtnClose: false
    },
    roundOfTimeout: 0,
    selectedAction: ''
  },
  getters: {
    getLookupMasterMap: (state) => {
      return state.lookupMasterMap
    },
    getLookupMasterMapMain: (state) => {
      return state.lookupMasterMapMain
    },
    getLookupMasterMapPlant: (state) => {
      return state.lookupMasterMapPlant
    },
    getLookupFncLocation: (state) => {
      return state.lookupFncLocation
    },
    getLookupUser: (state) => {
      return state.lookupUser
    },
    getLookupUser2: (state) => {
      return state.lookupUser2
    },
    selectedAction: (state) => {
      return state.selectedAction
    }
  },
  mutations: {
    setLookupMasterMap: (state, data) => {
      state.lookupMasterMap = data
    },
    setLookupMasterMapMain: (state, data) => {
      state.lookupMasterMapMain = data
    },
    setLookupMasterMapPlant: (state, data) => {
      state.lookupMasterMapPlant = data
    },
    setLookupFncLocation: (state, data) => {
      state.lookupFncLocation = data
    },
    setLookupUser: (state, data) => {
      state.lookupUser = data
    },
    setLookupUser2: (state, data) => {
      state.lookupUser2 = data
    },
    setOrder: (state, data) => {
      let setDefaultField = data.order
      setDefaultField.Order_Type__c = data.order.Order_Type__c || 'CR01'
      state.orderList = data.orderList
      state.order = Object.assign(state.order, setDefaultField)
    },
    setRoundTimeout: (state, numOfTime) => {
      state.roundOfTimeout = numOfTime
    },
    setSelectedAction: (state, data) => {
      state.selectedAction = data
    }
  },
  actions: {
    receiveLookupMasterMap: ({commit}, sendParam) => {
      createOrderApi.getLookupPMActivity(result => {
        commit('setLookupMasterMap', result)
      }, sendParam)
    },
    receiveLookupMasterMapMain: ({commit}, sendParam) => {
      createOrderApi.getLookupMasterMap(result => {
        commit('setLookupMasterMapMain', result)
      }, sendParam)
    },
    receiveLookupMasterMapPlant: ({commit}, sendParam) => {
      createOrderApi.getLookupMainWorkCenterPlan(result => {
        commit('setLookupMasterMapPlant', result)
      }, sendParam)
    },
    receiveLookupFncLocation: ({commit}, searchParam) => {
      createOrderApi.getLookupFncLocation(result => {
        commit('setLookupFncLocation', result)
      }, searchParam)
    },
    receiveLookupUser: ({commit}, sendParam) => {
      createOrderApi.getLookupUser(result => {
        commit('setLookupUser', result)
      }, sendParam)
    },
    receiveLookupUser2: ({commit}, sendParam) => {
      createOrderApi.getLookupUser2(result => {
        commit('setLookupUser2', result)
      }, sendParam)
    },
    receiveOrder: ({commit}, sendParam) => {
      console.log('receiveEmocApplication', sendParam)
      createOrderApi.getOrder(result => {
        commit('setOrder', result)
      }, sendParam)
    },
    receiveOrderDetail: ({dispatch, commit, state}, {sendParam, afterSet, closePopupLoader}) => {
      createOrderApi.getOrderDetail(result => {
        console.log('getOrderDetail', result)
        if ((result.Integration_Status__c === 'In Progress' || result.Integration_Status__c === '' || typeof result.Integration_Status__c === 'undefined') && state.roundOfTimeout < 5) {
          setTimeout(() => {
            dispatch('receiveOrderDetail', {sendParam, afterSet, closePopupLoader})
            commit('setRoundTimeout', state.roundOfTimeout + 1)
          }, 5000)
        } else {
          commit('setOrder', result)
          commit('setRoundTimeout', 0)
          afterSet()
          closePopupLoader()
        }
      }, sendParam)
    },
    putOrder: ({commit}, paramSave) => {
      // console.log('saveOrder paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createOrderApi.putOrder(result => {
          resolve(result)
        }, paramSave)
      })
    },
    deleteOrder: ({commit}, paramSave) => {
      // console.log('saveOrder paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createOrderApi.deleteOrder(result => {
          resolve(result)
        }, paramSave)
      })
    },
    saveOrder: ({commit}, paramSave) => {
      // console.log('saveOrder paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createOrderApi.saveOrder(result => {
          resolve(result)
        }, paramSave)
      })
    },
    receiveCloseTab: ({commit}, paramSave) => {
      console.log('receiveCloseTab paramSave', paramSave)
      return new Promise((resolve, reject) => {
        createOrderApi.closeTab(result => {
          resolve(result)
        }, paramSave)
      })
    },
    callCloseOrderStatus: ({commit}, paramSave) => {
      return new Promise((resolve, reject) => {
        createOrderApi.callCloseOrderStatus(result => {
          resolve(result)
        }, paramSave)
      })
    },
    sendMsg: ({ commit }, paramMsg) => {
      createOrderApi.sendMsg(result => {
        if (result) {
          console.log('sendMsg successfully', result)
        }
      }, paramMsg)
    }
  }
})



