<template>
  <div name="relate-list">
    <div class="margin-form">
      <div class="">
        <div class="box-item">
          <div class="box-position">
            <div class="list-icon-form">
              <i class="fa fa-bell list-icon" aria-hidden="true"></i>
            </div>
            <div style="margin-left: 10px;">
              <h2>Notification {{ notification ? 1 : 0 }}</h2>
            </div>
          </div>
           <div style="margin-left: 15px;" v-show="notification">
            <ul>
              <li class="list-style">
                <a @click="redirectLink(notification, 'linkGoBack')">{{ notification || '-' }}</a>
              </li>
            </ul>
          </div>
        </div>
        <div class="box-item">
          <div class="box-position">
            <div class="list-icon-form">
              <i class="fa fa-list-alt list-icon" aria-hidden="true"></i>
            </div>
            <div style="margin-left: 10px;">
              <h2>Sub Order {{ orderList.length }}</h2>
            </div>
          </div>
          <div v-show="orderList.length > 0" style="margin-left: 15px;">
            <ul>
              <li v-for="order in orderList" class="list-style">
                <a @click="redirectLink(order.Id, 'linkToSubOrder')">{{ order.Name || '-' }}</a><br/>
                <span >Status: {{ order.Order_Status__c || '-' }}</span><br/>
                <span >Description: {{ order.Description__c || '-' }}</span><br/>
              </li>
            </ul>
          </div>
        </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'relate-list',
  props: ['orderList', 'notification'],
  methods: {
    redirectLink (id, linkName) {
      this.$store.dispatch('sendMsg', {
        action: linkName,
        recordId: id
      })
    }
  }
}
</script>

<style scoped>
.margin-form {
  margin: 30px 30px 30px 30px;
}
.box-item {
  border: 1px solid rgb(229, 229, 229);
  box-shadow: 0 4px 2px -2px rgb(229 229 229);
  min-height: 60px;
  width: 70%;
  align-items: center;
  padding: 10px;
  padding: 10px;
  margin-bottom: 15px;
}
.box-position {
  display: flex;
}
.list-icon {
  font-size: 20px;
  color: white;
  background-color: #d94686;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list-icon-form {
  width: 30px;
  height: 30px;
}
.list-style {
  border: 1px solid rgb(198, 198, 198);
  padding: 10px;
  border-radius: 3px;
  margin-bottom: 10px;
  margin-top: 10px;
  box-shadow: 0 4px 2px -2px rgb(229 229 229);
}
</style>


