<template>
  <div name="Tab">
    <div>
      <div class="custom-back-ground-color">
   <!--      <div class="custom-margin-form-back">
          <button class="slds-button custom-button-back">
            <i class="fa fa-chevron-left" aria-hidden="true"></i>
          </button>
        </div> -->
        <div class="custom-position-header custom-margin-form">
            <div class="custom-position-header">
              <div class="list-icon-form">
                <i class="fa fa-list-alt list-icon" aria-hidden="true"></i>
              </div>
              <div style="margin-left: 10px;">
                <h2>Order</h2>
              </div>
            </div>
            <div>
              <select-action
                v-model="selectedAction"
                name="selected"
                :options="actionList"
                @onActionChange="onActionChange"
              >
              <!-- :handleAction="handleAction" -->
              </select-action>
            </div>
        </div>
      </div>
      <div class="custom-margin-form-tab">
        <div class="border-buttom">
          <button :class="['tab-button', tabSelect === 'detail' ? 'tab-button-selected tab-botton-border' : 'tab-button-none-selected']" @click="$emit('onTabChange','detail')">Detail</button>
          <button :class="['tab-button', tabSelect === 'relate' ? 'tab-button-selected tab-botton-border' : 'tab-button-none-selected']" @click="$emit('onTabChange','relate')">Relate</button>
        </div>
      </div>
    </div>  
  </div>
</template>
<script>
import SelectAction from '../standardSalesforceCmp/SelectAction.vue'
export default {
  components: { SelectAction },
  props: ['tabSelect', 'actionList'],
  emit: ['onTabChange'],
  methods: {
    onActionChange (value) {
      this.$emit('onActionChange', value)
    }
  },
  name: 'Tab',
  data () {
    return {
      selectedAction: ''
    }
  }
}
</script>

<style scoped>
  h2 {
    font-size: 20px;
  }
  .custom-position-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .custom-margin-form {
    padding: 0px 30px 20px 30px;
  }
  .custom-margin-form-back {
    padding: 10px 30px 10px 30px;
  }
  .custom-margin-form-tab {
    padding: 0px 30px 0px 30px;
  }
  .tab-button {
    width: 80px;
    height: 50px;
  }
  .tab-botton-border {
    border-right: 1px solid rgb(229, 229, 229);
    border-left: 1px solid rgb(229, 229, 229);
    border-bottom: none
  }
  .tab-button-selected {
    font-weight: 900;
    background-color: white;
  }
  .tab-button-none-selected {
    color: grey;
    background-color: rgb(250 250 250);
  }
  .custom-back-ground-color {
    background-color: rgb(229, 229, 229);
  }
  .custom-button-back {
    background-color: white;
    color: grey;
    border-color: #c9c9c9;
    width: 40px;
    height: 30px;
    justify-content: center;
    display: flex;
  }
  .list-icon {
    font-size: 20px;
    color: white;
    background-color: #1ab68b;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
  }
  .list-icon-form {
    width: 30px;
    height: 30px;
  }
  .border-buttom {
    border-bottom: 1px solid rgb(229, 229, 229);
  }
</style>


